@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&family=Roboto:wght@300&display=swap');
.nav-container {
  display: flex;
  justify-content: space-between;
  position: relative;
}
.navname {
  font-weight: 500;
  color: rgba(255, 0, 0, 0.689);
  margin-top: 0px;
  font-size: 50px;
}
.btn {
  height: 40px;
  width: 40px;
  cursor: pointer;
}

.button-container {
  background: transparent;
  border: none;
}

.btn:hover {
  border: solid;
  transition: all 0.1s ease;
}
.btn:hover::after {
  transition: all 0.4s ease;
}
.notclicked {
  display: none;
}
.clicked {
  transition: all 1s ease;
  background: rgba(0, 0, 0, 0.741);
  background-size: cover;
  background-repeat: no-repeat;
  position: fixed;
  top: -30px;
  width: 100vw;
  margin-left:-8px;
  height: 120vh;
  display: flex;
}
.close-container {
  margin-top: 10px;
  margin-left: 94%;
  width: 40px;
  border-radius: 50%;
}
.closebutton {
  width: 30px;
  height: inherit;
  background: transparent;
  cursor: pointer;
  color:white;
  border:solid;
  border-color: transparent;
  border-radius: 50%;
  padding:10px;
}
.closebutton:hover {
  border-color:white;
  transition: all 0.1s ease;
}
.close-1{
    flex:1;
}
.close-2{
    color:white;
    flex:2;
    background: rgba(0, 0, 0, 0.587);
    overflow-y: scroll;
    padding:100px;
}
.dropdown{
    display: block;
    position:relative;
}
select{
    display: block;
}
.Home{
    cursor:pointer;
    margin-top: 50px;
    font-size: 30px;
}
.homeclicked{
  transition:0.5s all ease;
    display: flex;
}
.rotatenow{
    transform: rotate(90deg);
}
.Shop-Home{
    cursor:pointer;
    margin-top: 50px;
    font-size: 30px;
}
.Pages-Home{
