.header-container{
    display:flex;
    overflow:hidden;
    justify-content: space-between;
    height:900px;
    width:100vw;
    margin-top:-100px;
    z-index:10;
}

.content{
    flex:1.1;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    flex-direction: column;
}
.banner-title{
    font-size:100px;
    font-weight:50;
    color: rgba(0, 0, 0, 0.773);
    line-height: 90px;
}

.link{
    font-size:100px;
    font-weight: 150;
    margin-top:-60px;
    color:rgba(255, 0, 0, 0.51);
}
.image-container{
    flex:1;
    background: url("../stuff/header-photo.jpg");
    background-size: cover;

}

.desc{
    margin-top:-50px;
    width:600px;
    font-size:20px;
}
.name{
    display: flex;
    font-size:150px;
    color:rgba(133, 118, 118, 0.071);
    margin-top:-150px;
    margin-left:550px;
}

