@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&family=Roboto:wght@300&display=swap');

.contact-container{
    display: flex;
}
.contact-desc{
flex:0.5;
font-size: 25px;
padding:50px;
}
.contact-sec1{
    flex:1;
    background-color: white;
    padding:10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.contact-sec2{
    flex:1;
    background-color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.sec-content{
    display: flex;
    flex-wrap: wrap;

}
.sec-point{
    padding:10px;
    font-size:15px;
    width:150px;
    color:rgba(121, 117, 117, 0.81);
    font-family: 'Poppins', sans-serif;
}
.sec-head{
    text-align: center;
    font-weight: 100;
}
.sec-con{
    color:rgba(121, 117, 117, 0.81);
    width:300px;
    margin-left:50px;
    font-family: 'Poppins', sans-serif;
}
.links{
    text-decoration: none;
    color:rgba(121, 117, 117, 0.81);
}
.links:hover{
    color:rgba(243, 40, 17, 0.612);
    transition:0.5s all ease;
}
.sec-footer{
    display: flex;

}
.foot-1{
    background:transparent;
    flex:1.1;
}
.foot-socials{
    background: white;
    flex:2;
    display: flex;
    justify-content: center;
    height:80px;
    line-height: 80px;
}
.foot-desc{
    flex:1;
    padding-left:20px;
    font-size:25px;
}
.socials{
    flex:1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding:30px;

}
.icons{
    height:40px;
    width:40px;
}
.social-links{
    color:black;
}
.social-links:hover{
    color:rgba(243, 40, 17, 0.612);
    transition:0.5s all ease;
}