@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&family=Roboto:wght@300&display=swap');

.awards-container{
    display: flex;
    padding-top:300px;
    margin-top:-100px;
}
.awards-heading{
    flex:1;
    font-size: 50px;
    display: flex;
    justify-content:center;
    align-items: center;
}
.awards{
    flex:1;
}
.awards-content{
    width:400px;
}
.award{
    display: flex;
    padding:30px;
    background: rgb(255, 255, 255);
    margin-bottom:40px;
}
.award-title{
    font-weight: 100;
    margin-top:-10px;
}
.side1{
    flex:1.5;
    padding:20px;

}
.side2{
    flex:1;
    display:flex;
    align-items:center;
}

.year{
    font-family: 'Poppins', sans-serif;
    font-weight: 100;
    color:rgba(5, 5, 5, 0.508)
}
.award-criteria{
    margin-top:10px;
    color:rgba(243, 40, 17, 0.612);
    font-family: 'Poppins', sans-serif;
}
.dash{
    color:rgba(0, 0, 0, 0.508);
}
.awards-side-bar{
    transform:rotate(270deg);
    font-size:150px;
    margin-top:-1300px;
    margin-left:-1100px;
    color:rgba(133, 118, 118, 0.071);
    font-weight: 200;
}