@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&family=Roboto:wght@300&display=swap');

.about-container{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color:rgb(250, 243, 242);
    padding-top:100px;
    height: 700px;
    margin-right:-10px;
    
}

.pic{
    flex:1;
    background: url("../stuff/mid.png");
    background-size: cover;
    height:600px;
    margin-left:40px;
}
.Aboutme{
    flex:1;
    margin:80px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction:column; 
}
.aboutheader{
    display: flex;
    justify-content: center;
    align-items: center;
    text-align:center;
    font-size:50px;
    font-weight:500;
}
.aboutdesc{
    margin-top:-20px;
    font-family: 'Poppins', sans-serif;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align:center;
    color:rgba(0, 0, 0, 0.511);
}
.mail{
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}
.About{
    transform: rotate(90deg);
    color:rgba(133, 118, 118, 0.071);
    font-family: 'Poppins', sans-serif;
    background: transparent;
    font-weight:500;
    font-size:150px;
    width:750px;
    margin-left:800px;
    margin-top:-500px;
}