@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&family=Roboto:wght@300&display=swap');


.myskills-container{
    margin-top:275px;
    background-color:rgb(250, 243, 242);
    padding-top:100px;
    margin-right:-10px;
    margin-top:-50px;

}
.contents{
    display: flex;
    justify-content:center;
    align-items:center;
    margin-top:-200px;
}

.content-desc{
    flex:1;
    font-size:60px;
    height:300px;
    font-weight: 100;

}
.tools{
    flex:1;
    height:300px;
}
.MySkills{
    font-size:150px;
    color:rgba(133, 118, 118, 0.071);
    font-weight: 100;
    height:300px;
}
li{
    list-style-type: none;
}
.tools-heading{
    font-weight: 100;
    font-size:25px;
    margin-bottom:50px;
    margin-left: 40px;
    
}

.tool{
    width:300px;
    display: flex;
    justify-content: space-between;
    margin-bottom:10px;
    margin-top:40px;
}

.tool-name{
    font-family: 'Poppins', sans-serif;
    color:rgba(5, 5, 5, 0.508)
}