@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&family=Roboto:wght@300&display=swap');


.skills-container{
    background-color:rgb(250, 243, 242);
    height:600px;
    margin-left: -10px;
    display: flex;
    justify-content: center;
    margin-right: -10px;
    margin-top:-25px;
    
}
.skills{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left:40px;
    
}
.skill{
    flex:1;
    background-color:white;
    margin-right: 30px;
    border-radius: 5%;
    padding:40px;
}

.skillname{
    font-weight: 200;
}
.skilldesc{
    color:rgba(0, 0, 0, 0.585);
    font-family: 'Poppins', sans-serif;
}

.image:hover{
    transform: translate(0,-10px);
    transition: all 0.5s ease;
}